// Inject Cide Styles
const domain = window.location.hostname;
const cideStyle = document.querySelector("#fw-cide-st");
const cideMarkup = document.querySelector("#fw-cide-mk");

if (domain !== "localhost" || domain !== "febreroinfo.github.io") {
  if (!cideStyle && !cideMarkup) {
    injectCideStyle();
    injectCideMarkup();
  }
} else {
  window.addEventListener("load", () => {
    injectCideStyle();
    injectCideMarkup();
  });
}

