function injectCideMarkup() {
  const cideMarkup = document.createElement("div");
  cideMarkup.setAttribute("id", "fw-cide-mk");
  cideMarkup.setAttribute("class", "cide__result");
  cideMarkup.innerHTML = `<p>It's time to kill some bugs!</p>`;

  document.body.append(cideMarkup);

  window.addEventListener("keydown", showResultInformation, false);
  window.addEventListener("keyup", showResultInformation, false);
  document.addEventListener("mouseover", updateResultInformation, false);
}

const resultInformation = document.getElementById("fw-cide-mk");

// Updates the result banner
function updateResultInformation(event) {
  let id = event.target.id.toString() || "";
  let classList = event.target.classList.toString() || "";
  let node = event.target.nodeName.toLowerCase() || "";

  if (!!!resultInformation) {
    return false;
  }

  // Combine the node name, classes, and id into a string in the banner
  let resultContent = `<p>You\'re hovering on = { node: <b> ${node} </b>, id: <b> ${id} </b>,  classes: <b> ${classList} </b> }</p>`;

  resultInformation.innerHTML = resultContent;
}

// Toggles the visibility of the banner
function showResultInformation(event) {
  if (event.ctrlKey) {
    resultInformation.classList.add("cide__active");
  } else {
    resultInformation.classList.remove("cide__active");
  }
}

// CSS Debugging via Keydown
document.addEventListener("keydown", (event) => {
  const page = document.documentElement;
  let message;
  // Activate Cide with Outline Option
  if (event.ctrlKey && event.key === "`") {
    if (!page.classList.contains("cide__on")) {
      page.setAttribute("class", "cide__on");
      message = "Debugging is ON";
      statusMessage(message);
    } else {
      page.removeAttribute("class");
      message = "Debugging is OFF";
      statusMessage(message);
    }
  }

  // Activate Cide with Outline and Box Shadow Options
  if (event.ctrlKey && event.key === "1") {
    if (
      !page.classList.contains("cide__enhanced") ||
      !page.classList.contains("cide__on")
    ) {
      page.setAttribute("class", "cide__on cide__enhanced");
      message = "Enchanced debugging is ON";
      statusMessage(message);
    } else {
      page.removeAttribute("class");
      message = "Enhanced debugging is OFF";
      statusMessage(message);
    }
  }

  // Deactivate Cide Options
  if (event.ctrlKey && event.key === "2") {
    if (
      page.classList.contains("cide__on") ||
      page.classList.contains("cide__enhanced")
    ) {
      page.removeAttribute("class");
      message = "Debugging is OFF";
      statusMessage(message);
    }
  }
});

function statusMessage(message) {
  // Clear message if it exist
  const statusMarkup = document.querySelector("#fw-cide-status-mk");
  if (statusMarkup) {
    statusMarkup.remove();
  }

  // Create message markup
  const status = document.createElement("div");
  status.setAttribute("id", "fw-cide-status-mk");
  status.setAttribute("class", "cide__status");
  const statusContent = document.createElement("span");
  statusContent.textContent = message;
  status.appendChild(statusContent);
  document.body.appendChild(status);

  // Remove message after it appears
  setTimeout(() => {
    status.remove();
  }, 3000);
}
